@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
.color-picker-container,.palette-container{background-color:rgba(255,255,255,0.8);padding:3rem;margin:1rem 0;border-top:solid 1rem rgba(255,255,255,0.3);border-bottom:solid 1rem rgba(255,255,255,0.3)}.base-color-hsl{display:flex;margin-top:2rem;justify-content:center}.base-color-hsl>*{margin:0 1rem}.palette{display:flex;justify-content:center}.swatch p{color:black;-webkit-text-fill-color:white;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black}html,body{margin:0;box-sizing:border-box;padding:0}h1,h2,p{font-family:'Permanent Marker', sans-serif;font-size:larger}.App{text-align:center;margin:6rem 0}

